/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { sprintf, __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import {
  useBlockProps,
  useInnerBlocksProps,
  InspectorControls,
  InnerBlocks,
  BlockControls,
  MediaUpload,
  MediaUploadCheck,
  store as blockEditorStore,
} from '@wordpress/block-editor';

import { PanelBody, RangeControl } from '@wordpress/components';

import { useSelect } from '@wordpress/data';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */
export default function Edit({ attributes, setAttributes, clientId }) {
  const { columnsIds, hasChildBlocks, rootClientId } = useSelect(
    (select) => {
      const { getBlockOrder, getBlockRootClientId } = select(blockEditorStore);

      const rootId = getBlockRootClientId(clientId);

      return {
        hasChildBlocks: getBlockOrder(clientId).length > 0,
        rootClientId: rootId,
        columnsIds: getBlockOrder(rootId),
      };
    },
    [clientId],
  );

  const blockProps = useBlockProps({
    className: `outline-1 outline-dashed hover:bg-slate-50 w-full p-4`,
  });

  const columnsCount = columnsIds.length;
  const currentColumnPosition = columnsIds.indexOf(clientId) + 1;

  const label = sprintf(
    /* translators: 1: Block label (i.e. "Block: Column"), 2: Position of the selected block, 3: Total number of sibling blocks of the same type */
    __('%1$s (%2$d of %3$d)'),
    blockProps['aria-label'],
  );

  const innerBlockProps = useInnerBlocksProps(
    {
      ...blockProps,
      'aria-label': label,
    },
    {
      templateLock: attributes.templateLock,
      renderAppender: hasChildBlocks
        ? undefined
        : InnerBlocks.ButtonBlockAppender,
    },
  );

  const onUpdateImage = (image) => {
    setAttributes({
      mediaUrl: image.url,
    });
  };

  return (
    <>
      <BlockControls></BlockControls>
      <div className="relative flex flex-shrink-0 w-full">
        <div {...innerBlockProps} />
      </div>
    </>
  );
}
